.wrapper {
	&__header {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin: 30px 0;
	}

	&__day-picker {
		display: flex;
		justify-content: center;
	}
}

.text {
	&__date {
		font-size: 18px;
		font-weight: bold;
		margin-bottom: 12px;
	}

	&__time {
		font-size: 15px;
		font-weight: 500;
	}
}
