.container {
	display: flex;
	padding: 8px;
}

.wrapper {
	flex: 1;
	display: flex;
	flex-direction: column;
	line-height: 1.3;
}

.button {
	width: 52px;
	height: 24px;
	border: 1px solid var(--color-black);
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 4px;
	background-color: #fff;
	font-weight: 400;
	margin-bottom: 6px;
	box-sizing: border-box;
	color: var(--color-black);
	cursor: pointer;
}
